@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,900);
@import url(https://fonts.googleapis.com/css?family=Merriweather);
@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,900);
@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,900);
@import url(https://fonts.googleapis.com/css?family=Merriweather);
@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,900);
@import url(https://fonts.googleapis.com/css?family=Merriweather);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


#home{
    background: #7a2828;  
    height: 100vh;
    max-width: 100%;
}
#home-container{
    margin: 0 auto;
    padding-left: 15%;
    padding-top:8%;
}
#home-iam, #home-name, #home-rol{
    color: #fff;
}
#home-iam, #home-rol{
    font-family: 'Roboto', sans-serif;
}
#home-iam{
    font-size:25px;
    font-weight: 300;
}
#home-rol{
    font-size:15px;
    font-weight: 300;
}
#home-name{
    font-family: 'Merriweather', serif;
    font-size: 145px;
    font-weight: 900;
}
#home-button{
    background: #fff;
    color: #7a2828;
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
    font-size: 15px;
    border: none;
}

nav{
    background: #7a2828; 
    font-family: 'Roboto', sans-serif;
    box-shadow: none;
}
nav a{
    color:#fff;
}
#about-tittle{
    font-family: 'Merriweather', serif;
    color: #7a2828;  
    font-weight: 900;
    font-size: 102px;
}
#skills-tittle{
    font-family: 'Merriweather', serif;
    color: #7a2828;  
    font-weight: 900;
    font-size: 102px;
}
#skills-text{
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
    font-size: 20px;
}




